.Accueil .lj-full{
  position: relative;
}
.Accueil .homehead{
  height:30vh;
  padding:1vh;
  display: flex;
  justify-content: space-between;
}
  
.Accueil .profilToph{
  box-shadow:0 0 0 .7vh #fff inset;
  border-radius: 100%;
  width:30vh;
  height:30vh;
  background: #888 url(/static/media/moi.8e3033d0.jpg);
  background-size: cover;
}
.Accueil{
  background-image: url(/static/media/cc.8358c315.jpg);
  background-size: 100% auto;
}
  
.Accueil .about{
  top: 0;
  font-weight: 100;
  height: 30vh;
  width: calc(100% - 45vh);
  text-shadow: 0 0 5px #333;
  color:#333;
  color:white;
  position:relative;
}
.Accueil .about .name{
  font-size: 5vw;
}

.Accueil .about .title{
  font-size: 4vw;
  text-align: center;
}

.Accueil .about .citation{
  font-size: 2.7vw;
  position:absolute;
  right: 1vh;
  text-align: right;
  bottom:0;
  width: 100%;
  color:#333;
  text-shadow: 0 0 5px #fff,0 0 5px #fff;
}
  
.Accueil .plan{
  height:68vh;
  display: flex;
  flex-direction: row;
  position: relative;
  justify-content: space-around;
  align-items: center;
  background: rgba(255,255,255,.5);
  padding: 0 1vh;
}
.Accueil .backplan{
  position: absolute;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  box-shadow: 0 0 2px 2px rgba(255,255,255,.5);
}
.Accueil .backplan>div{
  width: 3px;
  height: 150%;
  position: relative;
  overflow: hidden;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
}
.Accueil .backplan>div.monte>div{
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
}
.Accueil .backplan>div>div{
  width:100%;
  height: 100%;
}
.Accueil .backplan>div>div>div{
  position: absolute;
  left: 0;
  top: -5%;
  width:100%;
  height: 5%;
  background: rgb(46, 138, 243);
}
.Accueil .backplan>div.anim>div>div{
  -webkit-animation: backanim2 1.5s infinite;
          animation: backanim2 1.5s infinite;
}
@media only screen and (max-width:800px){
  .Accueil .homehead{
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
  }
  .Accueil .profilToph{
      width:20vh;
      height:20vh;
  }
  .Accueil .about{
      width:100%;
      text-align: center;
      color:#fff;
      height: auto;
  }
  .Accueil .about .name{
      font-size: 9vw;
  }

  .Accueil .about .title{
      font-size: 7vw;
      text-align: center;
  }

  .Accueil .about .citation{
      display: none
  }
  .Accueil .plan{
      flex-direction: column;
      padding:0;
      justify-content: space-evenly;
      align-items: center;
  }
  .Accueil{
      background-size: cover;
  }
}
  
.lj-bdg{
  height: 45vh;
  overflow: hidden;
  width:24vw;
  border-radius: 3px;
  background: white;
  cursor: pointer;
  position: relative;
  z-index: 2;
}
.lj-bdg:hover{
  opacity:1;
}
.lj-bdg:hover .lj-bdg-info{
  top:-30%;
}
.lj-bdg:hover .arrow{
  -webkit-transform: scale(1);
          transform: scale(1);
}
.lj-bdg>.lj-bdg-image{
  height: 60%;
  overflow: hidden;
}
.lj-bdg>.lj-bdg-info{
  color:#444;
  height: 55%;
  overflow: hidden;
  position:relative;
  top:-15%;
  -webkit-transition: top .3s;
  transition: top .3s;
}
.lj-bdg-title{
  font-weight: 200;
  height: 6.7vh;
  line-height: 9vh;
  font-size: 2vw;
  padding-left: 7px;
  text-shadow: 0 0 2px #fff;
  position: relative;
  z-index:5;
}
.lj-bdg-desc{
  font-size: 1.5vw;
  box-shadow: 0 0 5vh 3vh #fff,0 0 6vh 3vh #fff;
  background: rgb(255,255,255);
  padding: 7px;
  height: 16.5vh;
  position: relative;
  overflow: hidden;
}
.lj-bdg-image>.back{
  background-size: cover;
  width: 100%;
  height: 100%;
  z-index: 0;
  position: relative;
}
.lj-bdg-image.anim>.back{
  -webkit-animation: backanim 200s infinite;
          animation: backanim 200s infinite;
}
.lj-bdg-image>.back>.masque{
  position: absolute;
  width:100%;
  height:100%;
  background: rgba(0,0,0,.2);
}
.lj-bdg-desc.logo{
  text-align: center;
}
.lj-bdg .licon{
  width:15%;
  height: auto;
  margin-top:1vh;
}
.lj-bdg .licons{
  text-align: center;
}

.lj-bdg button.licon{
  width:95%;
  height:15vw;
  font-size: 6vw;
  background: none;
  border:2px solid #fff;
  color:white;
}
.lj-bdg .arrow{
  position:absolute;
  bottom:10px;
  width: 20%;
  opacity: .4;
  left:40%;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transition: all .2s;
  transition: all .2s;
  border-radius: 100%;
  box-shadow: 0 0 20px #bbb inset;
}
.lj-bdg .arrow:hover{
  opacity: 1;
  cursor: default;
}  
@media only screen and (max-width:100vh){
  /* 100vh */
  .lj-bdg:hover .lj-bdg-info{
      top:0%;
  }
  .lj-bdg-desc img{
      width:15%;
      height: auto;
  }
  .arrow{
      display:none;
  }
  .lj-bdg-image>.back{
      -webkit-animation: none;
              animation: none;
  }
  .lj-bdg{
      height: 14vh;
      width:95vw;
      overflow: hidden;
      display: flex;
      border-radius: 0px;
  }
  .lj-bdg>.lj-bdg-image{
      width:30%;
      height: 100%;
      overflow: hidden;
  }
  .lj-bdg>.lj-bdg-info{
      height: 100%;
      width:70%;
      overflow: hidden;
      background: #fff;
      top:0;
      padding:0;
      padding-top: 5px;
  }
  .lj-bdg-title,.lj-bdg-desc{
      padding:0 1vw;
  }
  .lj-bdg-title{
      line-height: 5vw;
      height:5vw;
      font-size: 5vw;
      text-shadow: 0 0 2px #fff;
  }
  .lj-bdg-desc{
      font-size: 4vw;
      background: auto;
  }
}

@-webkit-keyframes backanim{
  12.5%,42.5%,62.5%,87.5%{
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      -webkit-transform-origin: center;
              transform-origin: center;
  }
  25%,50%,75%,100%{
      -webkit-transform: scale(1);
              transform: scale(1);
  }
  25% {
      -webkit-transform-origin: bottom left;
              transform-origin: bottom left;
  }
  50%{
      -webkit-transform-origin: bottom right;
              transform-origin: bottom right;;
  }
  75%{
      -webkit-transform-origin: top right;
              transform-origin: top right;
  }
  100%{
      background-position: top left;
  }
}

@keyframes backanim{
  12.5%,42.5%,62.5%,87.5%{
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      -webkit-transform-origin: center;
              transform-origin: center;
  }
  25%,50%,75%,100%{
      -webkit-transform: scale(1);
              transform: scale(1);
  }
  25% {
      -webkit-transform-origin: bottom left;
              transform-origin: bottom left;
  }
  50%{
      -webkit-transform-origin: bottom right;
              transform-origin: bottom right;;
  }
  75%{
      -webkit-transform-origin: top right;
              transform-origin: top right;
  }
  100%{
      background-position: top left;
  }
}
@-webkit-keyframes backanim2{
  to{top:100%;}
}
@keyframes backanim2{
  to{top:100%;}
}
